.action-text {
  padding: 10px;
  max-width: 200px;
  margin: 10px;
  background-color: #f0f0f0;
  color: black;
  border: none;
  border-radius: 4px;
  margin:auto;
}

.action-text:hover {
  background-color: #e7e7e7;
  text-decoration: underline;
}

.error-box {
  color: red;
  font-size: 0.875rem;
  margin-top: 1rem;
}

.login-text {
  color: gray;
  font-size: 0.875rem;
  cursor: pointer;
  margin-top: 1rem;
  transition: color 0.3s ease, text-decoration 0.3s ease;
}

.login-text:hover {
  color: #b91c1c;
  text-decoration: underline;
}

